import { Component, OnInit, OnDestroy } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
// importing route related code
import {Location} from '@angular/common';
import {ActivatedRoute,Router} from '@angular/router';
import { BlogService } from '../blog.service';
import{BlogHttpService} from '../blog-http.service';

@Component({
  selector: 'app-view',
  templateUrl: './view.component.html',
  styleUrls: ['./view.component.css'],
  providers:[Location]
})
export class ViewComponent implements OnInit,OnDestroy {

  public currentBlog;

  
 
  
  
  constructor(private _route: ActivatedRoute, private router :Router,public blogService:BlogService ,public blogHttpService:BlogHttpService,private toastr:ToastrService,private location:Location)  {
console.log(" blog view constructor is calling");
   }

  ngOnInit() {
    console.log(" blog view ngOnInit called");
    //getting blog id from the route

  let myBlogId=this._route.snapshot.paramMap.get('blogId');
  console.log (myBlogId);

  //calling the function to get the blog id with this blog  ot of overall array
 /* this.currentBlog=this.blogService.getSingleBlogInformation(myBlogId);
  console.log(this.currentBlog);*/
  this.blogHttpService.getSingleBlogInformation(myBlogId).subscribe(

    data=>{
      console.log("logging Data")
      console.log(data);
      this.currentBlog=data["data"];
    },
    error=>{
    console.log("error message");
    console.log(error.errorMessage);
    }

  )

  }
  
  
 ngOnDestroy(){
  console.log("blog view componenet destroye ")
 }

 public deleteThisBlog():any{
  this.blogHttpService.deleteBlog(this.currentBlog.myBlogId).subscribe(

    data=>{
      console.log("logging Data");
      console.log(data);
     this.toastr.success('Blog Deleted Successfully!', 'Success!');
      setTimeout (()=>{
      this.router.navigate(['/home']);
      },2000)
    },
    error=>{
    console.log("error message");
    console.log(error.errorMessage);
    this.toastr.error('Some Error occured!', 'Oops!');
    }

  )

 }   //end of delete blog

 public goBackToPreviousPage():any{
   this.location.back();
 }



}
