import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
  
import { CommonModule } from '@angular/common';

 
import { ToastrModule } from 'ngx-toastr';

import { HttpClientModule } from '@angular/common/http';

//router module include for  setting up the route 
import{RouterModule,Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';

import { AppComponent } from './app.component';
import { MainComponent } from './main/main.component';
import { ViewComponent } from './view/view.component';
import { CreateComponent } from './create/create.component';
import { EditComponent } from './edit/edit.component';
import { provideForRootGuard } from '../../node_modules/@angular/router/src/router_module';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { NotfoundComponent } from './notfound/notfound.component';
import { BlogService } from './blog.service';
import { BlogHttpService } from './blog-http.service';

@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    ViewComponent,
    CreateComponent,
    EditComponent,
    HomeComponent,
    AboutComponent,
    NotfoundComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    BrowserAnimationsModule,
    CommonModule,
    BrowserAnimationsModule, // required animations module
    ToastrModule.forRoot() ,// ToastrModule added
    //router module is used to declare for possible route declareation
    RouterModule.forRoot([
  {path:'home',component:HomeComponent},
  {path:'',redirectTo:'home',pathMatch:'full'},
  {path:'about',component:AboutComponent},
  {path:'blog/:blogId',component:ViewComponent},
  {path:'create',component:CreateComponent},
  {path:'edit/:blogId',component:EditComponent},
  {path:'**',component:NotfoundComponent}
    ])
  ],
  providers: [BlogService,BlogHttpService],
  bootstrap: [AppComponent]
  
})
export class AppModule { }
