import { Component, OnInit,ViewContainerRef } from '@angular/core';
import{BlogHttpService} from '../blog-http.service';
import {ActivatedRoute,Router} from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import {Location} from '@angular/common';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {
  public currentBlog;
  public possibleCategories=["Comedy","Drama","Technologies","Action"];
  constructor(private blogHttpService:BlogHttpService,private _route: ActivatedRoute, private router :Router,private toastr:ToastrService) {
 
  }
  
  ngOnInit() {

    console.log(" blog edit ngOnInit called");
    //getting blog id from the route

  let myBlogId=this._route.snapshot.paramMap.get('blogId');
  console.log (myBlogId);

  //calling the function to get the blog id with this blog  ot of overall array
 /* this.currentBlog=this.blogService.getSingleBlogInformation(myBlogId);
  console.log(this.currentBlog);*/
  this.blogHttpService.getSingleBlogInformation(myBlogId).subscribe(

    data=>{
      console.log("logging Data")
      console.log(data);
      this.currentBlog=data["data"];
    },
    error=>{
    console.log("error message");
    console.log(error.errorMessage);
    }

  )

  } // end of ngonit function

  public editThisBlog():any{
    this.blogHttpService.editBlog(this.currentBlog.blogId,this.currentBlog).subscribe(
  
      data=>{
        console.log("logging Data");
        console.log(data);
       this.toastr.success('Blog Edited Successfully!', 'Success!');
        setTimeout (()=>{
          this.router.navigate(['/blog',this.currentBlog.blogId]);
        },2000)
      },
      error=>{
      console.log("error message");
      console.log(error.errorMessage);
      this.toastr.error('Some Error occured!', 'Oops!');
      }
  
    )
  
   }   //end of edit blog

}
